<template>
  <b-card>
    <div slot="header">
      <strong>{{ cardTitle }}</strong>
    </div>

    <b-row>
      <b-col sm="6" lg="3" v-for="(eachTemplate, templateIndex) in templates" v-bind:key="templateIndex">
        <b-card v-on:click="selectTemplate(eachTemplate.name)">
          <div class="h4 m-0">{{ eachTemplate.name }}</div>
          <b-progress height={} class="progress-xs my-3" variant="warning" :value="(eachTemplate.columns.length * 100)/6"/>
        </b-card>
      </b-col>
    </b-row>
  </b-card>
</template>

<script>
let templatesRef = firebase.database().ref('templates');
import store from '../store';

export default {
  name: 'manageTemplates',
  data() {
    return {
      templates: []
    };
  },
  props: ['cardTitle'],
  // firebase: {
  //   templates: templatesRef
  // },
  created: function() {
    templatesRef.on('child_added', snapshot => {
      this.templates.push(snapshot.val());
    });
  },
  methods: {
    selectTemplate(templateName) {
      store.commit('selectTemplate', templateName);
    }
  }
} // EXPORT

</script>

<style>

</style>



