import Vue from 'vue'
import Router from 'vue-router'

// Containers
import DefaultContainer from '@/containers/DefaultContainer'

// Views
// import Dashboard from '@/views/Dashboard'
import Templates from '@/views/Templates'
import CreatePost from '@/views/CreatePost'
import Food from '@/views/Food'
import Cabs from '@/views/Cabs'
import Briefcase from '@/views/Briefcase'

Vue.use(Router)

import store from '../store';

export default new Router({
  mode: 'history', // App is living in GitHub.io, so required!
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      // redirect: '/',
      name: 'Home',
      component: DefaultContainer,
      children: [
        // {
        //   path: 'dashboard',
        //   name: 'Dashboard',
        //   component: Dashboard
        // },
        {
          path: 'templates',
          name: 'Templates',
          component: Templates
        },
        {
          path: 'create-post',
          name: 'Create Post',
          beforeEnter: (to, from, next) => {
            store.commit('clearSelected');
            next()
          },
          component: CreatePost
        },
        {
          path: 'briefcase',
          name: 'Briefcase',
          component: Briefcase
        },
        {
          path: 'food',
          name: 'Food',
          component: Food
        },
        {
          path: 'cabs',
          name: 'Cabs',
          component: Cabs
        }
      ]
    }
  ] // ROUTES
})



