<template>
  <div class="animated fadeIn">
    <manage-templates card-title="Choose a Template" v-if="templateSelected === ''" />
    <new-record v-if="templateSelected !== ''"></new-record>
  </div>
  
</template>

<script>

import ManageTemplates from '@/views/ManageTemplates'
import NewRecord from '@/views/NewRecord'

import store from '../store';

export default {
  name: 'createPost',
  components: { ManageTemplates, NewRecord },
  // data() {
  //   return {
  //     // templateSelected: []
  //   }
  // },
  // methods: {
  // },
  // created: function() {
  // },
  computed: {
    templateSelected: () => store.state.selectedTemplateName
  }
}
</script>

