<template>
    <b-card>
      <div slot="header">
        <strong>Create New Template</strong>
        <span class="animated fadeIn" v-show="vmColumnsTable.length > 0">
          <b-button type="submit" variant="primary" v-on:click="clkSaveTemplate">Save Template</b-button>
          <b-button type="button" variant="secondary" v-on:click="clkResetTemplate">Reset</b-button>
        </span>
      </div>
      <b-row>
        <b-col sm="6">
          <b-row>
            <b-col sm="8">
              Is Data template? <c-switch class="mx-1" color="danger" v-model="vmIsDataTemplate" variant="pill" />
            </b-col>
          </b-row>
          <b-row>
            <b-col sm="8">
              <b-form-group>
                <b-input-group>
                  <b-input-group-prepend>
                    <b-input-group-text><i class="fa fa-envelope-o"></i></b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input type="text" placeholder="Template Name" title="eg: FoodTemplate1" v-model="vmTmplName"></b-form-input>
                </b-input-group>
                </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col sm="8">
              <b-form-group>
                <label for=""><strong>Columns</strong></label>
                <span v-for="(eachColumn, colIndex) in vmColumnControls" v-bind:key="colIndex">
                  <div class="cursor-pointer" v-show="vmColumnsTable[colIndex] !== undefined" v-on:click="clkRemoveColumn(colIndex)">- Remove</div>
                  <div class="cursor-pointer" v-show="vmColumnsTable[colIndex] !== undefined" v-on:click="clkUpdateColumn(colIndex)">= Update</div>
                  <b-form-input type="text" placeholder="eg: Full Name" v-model="vmColumnControls[colIndex]" v-show="colIndex < 6"></b-form-input>
                </span>
                <span class="cursor-pointer" v-show="checkColumns()" v-on:click="clkAddColumn">+ Add column</span>
              </b-form-group>
            </b-col>
          </b-row>
        </b-col>
        <b-col sm="6">
          <div v-if="vmColumnsTable.length > 0 && vmTmplName !== ''">
            <strong>{{ vmTmplName }}</strong> ( sample )

            <b-table :hover="true" :striped="true" :bordered="true" :small="true" 
            :fixed="true" responsive="sm" :fields="vmColumnsTable">
              <!-- <template slot="status" slot-scope="data"> -->
                <!-- <b-badge :variant="getBadge(data.item.status)">{{data.item.status}}</b-badge> -->
              <!-- </template> -->
            </b-table>

            <!-- <table>
              <thead>
              <tr>
                <th>No.</th>
                <th v-for="(colName, colIndex) in vmColumnsTable" v-bind:key="colIndex">
                  <span>{{ vmColumnsTable[colIndex] }}</span>
                  </th>
              </tr>
              </thead>
              <tbody>
                <tr v-for="n in 6" v-bind:key="n">
                  <td>{{n}}</td>
                  <td v-for="(colName, colIndex) in vmColumnsTable" v-bind:key="colIndex">col {{ colIndex + 1 }}</td>
                </tr>
              </tbody>
            </table> -->
          </div>
        </b-col>
      </b-row>
    </b-card>
</template>

<script>

// TO DO:
// 1. script to restrict entering spaces or special characters
// 2. CSS changes - Table format, Add/Remove buttons, Add/Remove buttons alignment-color
// 3. Check for unique column names

import { Switch as cSwitch } from '@coreui/vue'

// BUG: v-for doesnot update sample table upon update
let templateRef = firebase.database().ref('templates');

export default {
  name: 'createTemplate',
  components: {cSwitch},
  data() {
    return {
      vmEnableSave: false,
      vmTmplName: '',
      vmIsDataTemplate: false,
      vmColumnControls: [''],
      vmColumnsTable: []
    };
  },
  methods: {
    clkSaveTemplate() {
      let varPostType = this.vmIsDataTemplate ? 'data' : 'post';
      templateRef.push({name: this.vmTmplName, columns: this.vmColumnsTable, postType: varPostType});
      this.vmTmplName = '' ;
      this.vmColumnControls = [''];
      this.vmColumnsTable = [];
      this.vmEnableSave = false;
    },
    clkResetTemplate() {
      this.vmTmplName = '' ;
      this.vmColumnControls = [''];
      this.vmColumnsTable = [];
      this.vmEnableSave = false;
    },
    checkColumns() {
      let isValidflag = true;
      if ( this.vmColumnControls.length > 6 ) {
        return false;
      }

      for (let i=0; i < this.vmColumnControls.length; i++) {
        if ( this.vmColumnControls[i] === '' ) {
          isValidflag = false;
        }
      } // FOR
      return isValidflag;
    },
    clkRemoveColumn(colIndex) {
      this.vmColumnControls.splice(colIndex,1);
      this.vmColumnsTable.splice(colIndex,1);
    },
    clkUpdateColumn(colIndex) {
      this.vmColumnsTable[colIndex] = this.vmColumnControls[colIndex];
      // this.$nextTick(function () {
      //   this.vmColumnsTable[colIndex] === this.vmColumnControls[colIndex];
      // })
    },
    clkAddColumn() {
      let pushIndex = this.vmColumnControls.length - 1;
      this.vmColumnControls.push('');
      this.vmColumnsTable.push( this.vmColumnControls[pushIndex] );
    }
  } // METHODS
} // EXPORT

</script>

<style>

</style>



