import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        selectedTemplateName: ''
    },
    mutations: {
        selectTemplate(state, templateName) {
            state.selectedTemplateName = templateName;
        },
        clearSelected(state) {
            state.selectedTemplateName = '';
        }
    }
})



